import React, { useState, useEffect } from 'react';
import './header.css';
import logo from '../Images/Hari_Hat_logo2.png';
import logo2 from '../Images/Maharaj_ji.jpeg';
import Thoughts from '../Body/thoughts';

const Header = () => {
  const [backgroundLoaded, setBackgroundLoaded] = useState(false); 

  useEffect(() => {
    const img = new Image();
    img.src = require('../Images/header-back1.jpg'); 
    img.onload = () => {
      setBackgroundLoaded(true); 
    };
  }, []);

  return (
    <header className="header" style={backgroundLoaded ? { backgroundImage: `url(${require('../Images/HariHat_back.jpeg')})` } : {}}>
      {!backgroundLoaded && <div className="loader">Loading...</div>} 

     
    </header>
  );
};

export default Header;
